import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { platform, IOS, Gallery, Group, Header } from '@vkontakte/vkui';
import bridge from '@vkontakte/vk-bridge';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import './Games.css';
import axios from 'axios-jsonp-pro';
import sleep from '@react-corekit/sleep';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';



const osName = platform();




const Games = props => {

	const [fetchedfriends, setFriends] = useState([]);
	const [fetchedGroups, setGroups] = useState([]);
	const [fetchedGames, setGames] = useState([]);
	const [allGames, setAll] = useState("test");


	useEffect(() => {

		async function fetchData() {

			const token = await bridge.send("VKWebAppGetAuthToken", { "app_id": 7401331, "scope": "friends,video, groups" });
			console.log(token);
			let urlFriends = 'https://api.vk.com/method/friends.get?user_ids=' +
				props.fetchedUser.user_id +
				'&access_token=' + props.fetchedToken +
				'&order=random' +
				'&fields=name,last_name,photo_100' +
				'&count=20' +
				'&name_case=ins' +
				'&v=5.103';

			let gameStage = [];
			let friends = await axios.jsonp(urlFriends);

			let massivFriends = friends.response.items;



			//console.log(token.access_token);
			let user_ids = '';
			massivFriends.forEach(function (item, ) {
				user_ids += item.id + ",";

			});
			setFriends(massivFriends);
			//console.log(user_ids);
			for (const friend of massivFriends) {

				if (gameStage.length == 5) break;

				if (friend.deactivated) {
					continue;
				}

				let useFriends = false;
				for (const game of gameStage) {
					console.log(game);
					if (game.user.id == friend.id || game.friend.id == friend.id) {
						useFriends = true;
					}
				}

				if (useFriends) continue;

				let urlGroup = 'https://api.vk.com/method/groups.get?user_id=' +
					friend.id +
					'&access_token=' + props.fetchedToken +
					'&extended=1' +
					'&fiter=publics,group' +
					'&count=15' +
					'&v=5.103';
				//console.log(friend);
				let groups = await axios.jsonp(urlGroup);

				console.log(groups);
				if (groups.response) {

					let massivGroups = groups.response.items.sort(function () {
						return Math.random() - 0.5;
					});

					for (const group of massivGroups) {

						if (group.is_closed == 1) {
							continue;
						}

						let friendsToGame = null;
						await sleep(1000);
						let urlCheckGroups = 'https://api.vk.com/method/groups.isMember?user_ids=' +
							user_ids +
							'&group_id=' + group.id +
							'&access_token=' + props.fetchedToken +
							'&v=5.103';

						console.log(group);
						let isMembers = await axios.jsonp(urlCheckGroups);
						let resultMembers = [];
						if (isMembers.response) {
							resultMembers = isMembers.response.filter(function (memberGroup) {
								return memberGroup.member == 0;
							});

							if (resultMembers.length > 0) {

								// чтобы на последнем этапе не попался тот же самый пользователь.
								let randomMembers = resultMembers.sort(function () {
									return Math.random() - 0.5;
								});
								let user = massivFriends.filter(function (memberGroup) {
									return memberGroup.id == resultMembers[0].user_id;
								});
								gameStage.push(
									{ 'user': user[0], 'group': group, 'friend': friend });
								fetchedGroups.push(group);
								break;
							}


						}

					}

				}

			}
			setGames(gameStage);
			setGroups(fetchedGroups);
			console.log(gameStage);
		}
		fetchData();
	}, []);


	return (
		<Panel id={props.id}>
			<Group>
				<Gallery
					slideWidth="100%"
					style={{ height: document.documentElement.clientWidth * 0.8 }}
					bullets="dark"
				>
					{fetchedGames.length > 0 &&
						fetchedGames.map(item => (
							<div class="games_raund">
								<div class="about_group">
									<img class="group_img" src={item.group.photo_200} />
									<p>{item.group.name}</p>
								</div>
								<div class="about_group">
									<img class="group_img" src={item.friend.photo_100} />
									<p>{item.friend.last_name}</p>
								</div>
								<div class="about_group">
									<img class="group_img" src={item.user.photo_100} />
									<p>{item.user.last_name}</p>
								</div>
							</div>
							))
					}


				</Gallery>
			</Group>
		</Panel>)
};

Games.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Games;


